@import "_normalize.scss";
@import "_variables.scss";
@import "_sprite.scss";
@import "_slick.scss";

body {
	font: 16px/24px $gotham;
	color: #333333;
	background: #fefdfb;
	letter-spacing: 0.05em;
}

.site-header {
	background: url(../img/header-paper.png) top center no-repeat;
	padding-bottom: 70px;
}

.large-container {
	padding: 0 110px;
	position: relative;
	&__inner {
		position: relative;
	}
}

.container {
	max-width: 1160px;
	margin: auto;
	padding: 0 10px;
}

.main-menu {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 35px 0 0;
	padding: 0;
	list-style-type: none;
	a {
		text-transform: uppercase;
		color: #333333;
		font: 18px $bebas;
		letter-spacing: 0.1em;
		text-decoration: none;
		border-bottom: 1px solid transparent;
		transition: $transition;
	}
	a:hover {
		border-color: #77d5ad;
	}
}

.logo {
	position: absolute;
	left: 0;
	top: 20px;
	z-index: 2;
}

.anonses-link {
	position: absolute;
	right: 0;
	top: 20px;
	display: block;
	width: 180px;
	height: 50px;
	background-color: #ffe578;
	border-radius: 2px;
	color: #333333;
	font: 20px/50px $bebas;
	letter-spacing: 0.1em;
	text-align: center;
	text-decoration: none;
	transition: $transition;
	&:hover {
		background-color: #f9d642;
	}
}

.header-slider {
	margin-top: 40px;
	.slick-slide {
		position: relative;
	}
	.slick-dots {
		margin: 60px 0 0;
		padding: 0;
		list-style-type: none;
		text-align: center;
		li {
			display: inline-block;
			margin: 0 7px;
			width: 8px;
			height: 8px;
			button {
				font-size: 0px;
				text-indent: -9999px;
				width: 8px;
				height: 8px;
				border-radius: 50%;
				border: 1px solid #4c4a55;
				display: block;
				background: transparent;
				padding: 0;
				cursor: pointer;
				&:focus {
					outline: 0;
				}
			}
			&.slick-active button {
				background: #4c4a55;
			}
		}
	}
}

.header-slider__quote {
	font: 32px $lazy;
	color: #3f0da4;
	float: right;
	margin: 0 270px 50px 0;
	span {
		font: 300 18px $gotham;
		display: block;
		text-align: center;
		margin-top: 5px;
	}
}
.heading__img {
	img {
		display: block;
		max-width: 100%;
		height: auto;
		margin: auto;
	}
}

.anons__heading {
	font: 500 36px $gotham;
	color: #44424d;
	text-transform: uppercase;
	text-align: center;
	margin: 15px 0 40px;
	letter-spacing: 0.1em;
}

.watch-trailer {
	width: 258px;
	height: 50px;
	background-color: #9ccc65;
	border-radius: 2px;
	display: block;
	margin: auto;
	text-align: center;
	color: #fff;
	font: 700 20px/52px $bebas;
	text-decoration: none;
	letter-spacing: 0.1em;
	transition: $transition;
	span {
		padding-right: 26px;
		background: url(../img/watch-ico.png) right center no-repeat;
	}
	&:hover {
		background: #8abb51;
	}
}

.crow {
	position: absolute;
}

.header-slider__item--first {
	.crow1 {
		top: 0;
		left: 250px;
	}
	.crow2 {
		bottom: 0;
		left: 50px;
	}
	.crow3 {
		bottom: 0;
		right: 150px;
	}
}

.header-slider__item--second {
	.text-side {
		padding-right: 450px;
		padding-left: 100px;
	}
	.description-slide {
		text-align: center;
		position: relative;
		padding-bottom: 100px;
	}
	.madeby {
		font: 26px/30px $lazy;
		color: #44424d;
	}
	.book-title {
		margin: 30px 0 0;
		font: 45px $bask;
		color: #44424d;
		span {
			display: block;
		}
		.sep {
			font-size: 24px;
			line-height: 15px;
		}
		.big-title {
			font: 90px/90px $milio;
			color: #771010;
			text-transform: uppercase;
			letter-spacing: 0.05em;
		}
	}
	.book-img {
		position: absolute;
		right: 50px;
		top: 0;
	}
	.second__slide-links {
		text-align: center;
		p {
			margin: 0 0 15px;
			font: 14px $gotham;
			color: #44424d;
		}
	}
}

.header-slider__item--second {
	.crow4 {
		top: 0;
		left: 250px;
	}
	.crow5 {
		bottom: 0;
		left: 50px;
	}
	.crow6 {
		bottom: 0;
		right: 150px;
	}
}

.header__madeby {
	margin-top: 75px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	&--item {
		width: 20%;
		&.rating {
			padding-left: 60px;
			background: url(../img/rating.png) left top no-repeat;
		}
	}
	span {
		display: block;
	}
	.heading {
		font: 300 12px $gotham;
		color: #333333;
		margin-bottom: 5px;
	}
	.description {
		font: 16px $bebas;
		text-transform: uppercase;
		color: #333333;
	}
}

.home-slider {
	overflow: hidden;
	position: relative;
	z-index: 1;
	margin-top: -40px;
	&__helper {
		background: url(../img/home-slider.jpg) bottom center no-repeat;
		transform: rotate(1deg);
		margin-left: -10px;
		padding-left: 10px;
		margin-top: 20px;
		padding-bottom: 80px;
		margin-bottom: 20px;
	}
	&__inner {
		transform: rotate(-1deg);
	}
	&__container {
		max-width: 1250px;
		margin: auto;
	}
	.trailer__title {
		text-transform: uppercase;
		text-align: center;
		margin: 80px 0 40px;
		color: #333333;
		font: 700 40px $bebas;
		letter-spacing: 0.05em;
	}
}

.home-slider__trailer {
	padding: 0 55px;
	position: relative;
	&--inner {
		position: relative;
	}
	iframe {
		position: absolute;
		top:0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	.slider-item__container {
		position: relative;
		padding-bottom: 56.25%;
		padding-top: 35px;
		height: 0;
		overflow: hidden;
	}
	.slider-item__title {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		padding: 15px 0;
		text-transform: uppercase;
		color: #fff;
		text-align: center;
		font: 700 20px $bebas;
		background: rgba(180, 215, 124, .8);
		letter-spacing: 0.05em;
		pointer-events: none;
	}
	.slick-arrow {
		position: absolute;
		font-size: 0px;
		text-indent: -9999px;
		width: 15px;
		height: 24px;
		top: 50%;
		transform: translateY(-50%);
		padding: 0;
		outline: 0;
		border: none;
		cursor: pointer;
		&.slick-prev {
			background: url(../img/trailer-slider-l.png);
			left: 0;
		}
		&.slick-next {
			background: url(../img/trailer-slider-r.png);
			right: 0;
		}
	}
}

.random-quote {
	position: absolute;
	width: 256px;
	text-align: center;
	color: #3f0da4;
	transform: rotate(-6deg);
	&__text {
		font: 32px/36px $lazy;
	}
	&__author {
		margin-top: 15px;
		font: 300 18px $gotham;
	}
}

.about {
	padding: 80px 0;
	background: url(../img/header-paper.png) center center no-repeat;
	.random-quote:first-of-type {
		bottom: 10px;
	}
	.random-quote:nth-of-type(2) {
		right: 0;
		top: 50%;
	}
	&__top {
		position: relative;
	}
	&__description {
		font: 16px/28px $gotham;
		text-align: center;
		color: #333333;
		letter-spacing: 0.05em;
	}
	.links-wrap {
		margin-top: 50px;
		text-align: center;
	}
	&__links {
		margin-top: 100px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		&--item {
			width: 48%;
			position: relative;
			text-decoration: none;
			&:hover {
				.about__links--item-description {
					background: rgba(180, 215, 124, 1);
				}
			}
			img {
				display: block;
				max-width: 100%;
				height: auto;
			}
			&-description {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				background: rgba(180, 215, 124, .8);
				text-align: center;
				letter-spacing: 0.05em;
				text-transform: uppercase;
				color: #fff;
				font: 700 20px $bebas;
				padding: 10px 0;
				transition: $transition;
			}
		}
	}
}

.bordered__title {
	letter-spacing: 0.05em;
	font: 700 60px $bebas;
	text-transform: uppercase;
	text-align: center;
	span {
		position: relative;
		&::after,
		&::before {
			content: '';
			width: 200px;
			height: 2px;
			background: #44424d;
			position: absolute;
			top: 50%;
			margin-top: -1px;
		}
		&::after {
			right: -260px;
		}
		&::before {
			left: -260px;
		}
	}
}

.authors-wrapper {
	overflow: hidden;
}

.author__subheading {
	font: 700 40px $bebas;
	text-transform: uppercase;
	text-align: center;
	margin: 80px 0 30px;
}

.authors {
	background: url(../img/authors-bg.jpg) top center no-repeat;
	transform: rotate(1deg);
	margin: 20px 0 20px -10px;
	padding: 60px 0 60px 10px;
	.links-wrap {
		text-align: center;
	}
	&__inner {
		transform: rotate(-1deg);
	}
	&__title {
		letter-spacing: 0.05em;
		font: 700 60px $bebas;
		text-transform: uppercase;
		text-align: center;
		span {
			position: relative;
			&::after,
			&::before {
				content: '';
				width: 200px;
				height: 2px;
				background: #44424d;
				position: absolute;
				top: 50%;
				margin-top: -1px;
			}
			&::after {
				right: -260px;
			}
			&::before {
				left: -260px;
			}
		}
	}
	&__top {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		&--item {
			width: 48%;
			margin: 0;
			font: 16px/24px $gotham;
		}
	}
	&__team {
		text-align: center;
		list-style-type: none;
		margin: 0 0 50px;
		padding: 0;
		li {
			margin-bottom: 12px;
		}
	}
}

.home-news {
	margin: 70px 0 0;
	&__title {
		font: 700 40px $bebas;
		text-align: center;
	}
	&__list {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		&-item {
			width: 20%;
			padding: 5px;
			position: relative;
			&:hover {
				.inner {
					transform: translateY(0);
				}
			}
			.holder {
				background-size: cover;
				background-repeat: no-repeat;
				background-position: top center;
				overflow: hidden;
				height: 100%;
				position: relative;
				min-height: 380px;
			}
			.inner {
				position: absolute;
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				background: rgba(151, 202, 155, .9);
				padding: 50px;
				display: flex;
				align-items: center;
				text-align: center;
				transform: translateY(100%);
				transition: $transition;
				.heading {
					font: 700 20px/24px $bebas;
					margin: 0 0 10px;
					text-transform: uppercase;
				}
				.descr {
					font: 300 12px/16px $gotham;
					margin: 0;
				}
			}
			.full-link {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 1;
			}
		}
	}
}

.home-subscribe {
	padding: 15px 0;
	background: #ffe578;
	text-align: center;
	&__inner {
		border-radius: 2px;
		background: #fff;
		margin: auto;
		padding: 10px 30px;
		display: inline-block;
	}
	.title {
		display: inline-block;
		vertical-align: middle;
		margin-right: 30px;
		font: 400 14px $gotham;
	}
	
}

.site-footer {
	background: url(../img/header-paper.png) bottom center no-repeat;
	padding-top: 50px;
	&__wrapper {
		position: relative;
	}
	.random-quote:nth-of-type(1) {
		bottom: 100px;
		left: 0;
	}
	.random-quote:nth-of-type(2) {
		bottom: 100px;
		right: 0;
		transform: rotate(10deg)
	}
	&__inner {
		display: flex;
		flex-direction: row;
		&-col {
			width: 25%;
			padding: 0 20px 0 40px;
			&.last-col {
				background: url(../img/footer-crow.png) center bottom no-repeat;
			}
			.footer-logo {
				img {
					margin: auto;
					display: block;
					max-width: 100%;
					height: auto;
				}
			}
			.heading {
				margin: 0 0 40px;
				font: 700 20px $bebas;
				color: #44424d;
			}
			ul {
				list-style-type: none;
				margin: 0 0 30px;
				padding: 0;
				li {
					margin-bottom: 5px;
				}
				a {
					font: 400 14px $gotham;
					color: #4e5268;
					text-decoration: none;
					&:hover {
						color: #96ca9a;
					}
				}
			}
			.copyright,
			.footer-address {
				margin: 0 0 10px;
				font: 400 14px $gotham;
				color: #4e5268;
			}
			.footer-email,
			.footer-phone {
				margin: 0 0 10px;
				font: 400 14px $gotham;
				color: #4e5268;
				text-decoration: none;
			}
			.footer-address {
				margin-top: 10px;
				margin-bottom: 20px;
			}

		}
	}
}

.footer-bottom {
	border-top: 1px solid #101115;
	padding: 20px 0;
	p {
		font: 300 14px $gotham;
		color: #4e5268;
		margin: 0;
	}
	&__copyright {
		float: left;
	}
	&__madeby {
		float: right;
		a {
			color: #4dbc8c;
			font-size: 14px;
		}
	}
}

.pagination {
	font: 400 16px $bebas;
	text-transform: uppercase;
	margin: 40px 0;
	a {
		text-decoration: none;
		color: #333;
	}
	span {
		border-bottom: 1px solid #77d5ad;
		padding-bottom: 3px;
	}
	.separator {
		border: none;
		margin: 0 10px;
	}
}

.blog__news {
	background: url(../img/header-paper.png) bottom -90px center no-repeat;
	.links-wrap {
		text-align: center;
		margin-top: 30px;
	}
	.random-quote:nth-of-type(1) {
		left: 0;
		top: 200px;
	}
	.random-quote:nth-of-type(2) {
		right: 0;
		top: 500px;
		transform: rotate(8deg);
	}
	.random-quote:nth-of-type(3) {
		right: 0;
		top: 50%;
	}
	.random-quote__text {
		font-size: 27px;
	}
	.blog-crow2 {
		position: absolute;
		top: 60%;
		left: 0;
		max-width: 300px;
		height: auto;
	}
}

.blog__top {
	overflow: hidden;
	position: relative;
	z-index: 1;
	margin-top: -40px;
	&-helper {
		background: url(../img/home-slider.jpg) top center no-repeat;
		transform: rotate(1deg);
		margin-left: -10px;
		padding-left: 10px;
		margin-top: 20px;
		padding-bottom: 80px;
		margin-bottom: 20px;
		padding-top: 80px;
	}
	&-inner {
		transform: rotate(-1deg);
	}
	.description {
		line-height: 28px;
	}
}

.blog__inner {
	padding: 20px 0 40px;
	position: relative;
}

.blog__news-title {
	font: 700 40px $bebas;
	text-align: center;
	text-transform: uppercase;
}

.blog-big__item.blog__item {
	width: 100%;
	padding: 0;
	.blog__item-description {
		padding: 30px 120px;
	}
	.btn {
		margin: 0;
		float: right;
	}
	.description {
		margin-bottom: 30px;
	}
}

.blog__list {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin-top: 50px;
	.blog__item {
		width: 50%;
		&:nth-child(odd) {
			padding-right: 20px;
		}
		&:nth-child(even) {
			padding-left: 20px;
		}
	}
}

.blog__item {
	img {
		display: block;
		max-width: 100%;
		height: auto;
	}
	&-description {
		padding: 30px 0;
	}
	.date {
		font: 400 12px $gotham;
		color: #4e5268;
	}
	.heading {
		margin: 5px 0 15px;
		font: 700 20px $bebas;
		a {
			color: #333;
			text-decoration: none;
		}
	}
	.description {
		font: 300 12px/21px $gotham;
	}
	.img-side {
		height: 270px;
		background-position: center top;
		background-repeat: no-repeat;
		background-size: cover;
		position: relative;
		.full-link {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}
	}
}

































